import React from 'react';

export default function CardArCondicionado({ dispositivo, onToggle, onUpdate }) {
  return (
    <div className="border p-4 rounded bg-blue-50 shadow space-y-3">
      <h2 className="text-lg font-semibold">{dispositivo.nome}</h2>

      {/* Switch */}
      <label className="flex items-center cursor-pointer">
        <div className="relative">
          <input
            type="checkbox"
            className="sr-only"
            checked={dispositivo.estado.ligado}
            onChange={onToggle}
          />
          <div className={`block w-14 h-8 rounded-full ${dispositivo.estado.ligado ? 'bg-green-500' : 'bg-gray-300'}`}></div>
          <div className={`dot absolute left-1 top-1 w-6 h-6 rounded-full bg-white transition ${dispositivo.estado.ligado ? 'translate-x-6' : ''}`}></div>
        </div>
        <span className="ml-3">{dispositivo.estado.ligado ? 'Ligado' : 'Desligado'}</span>
      </label>

      {/* Temperatura */}
      <div>
        <label>Temperatura: {dispositivo.estado.temperatura}°C</label>
        <input
          type="range"
          min="16"
          max="30"
          value={dispositivo.estado.temperatura}
          onChange={(e) => onUpdate('temperatura', Number(e.target.value))}
          className="w-full accent-blue-500"
        />
      </div>

      {/* Modo */}
      <div>
        <label>Modo:</label>
        <select
          value={dispositivo.estado.modo}
          onChange={(e) => onUpdate('modo', e.target.value)}
          className="ml-2 border rounded p-1"
        >
          <option value="refrigerar">Refrigerar</option>
          <option value="aquecer">Aquecer</option>
          <option value="ventilar">Ventilar</option>
        </select>
      </div>

      <p>Consumo: {dispositivo.estado.consumoWatts}W</p>
    </div>
  );
}
