import React from 'react';

export default function CardEletrodomestico({ dispositivo, onToggle, onUpdate }) {
  return (
    <div className="border p-4 rounded bg-green-50 shadow space-y-3">
      <h2 className="text-lg font-semibold">{dispositivo.nome}</h2>

      {/* Switch */}
      <label className="flex items-center cursor-pointer">
        <div className="relative">
          <input
            type="checkbox"
            className="sr-only"
            checked={dispositivo.estado.ligado}
            onChange={onToggle}
          />
          <div className={`block w-14 h-8 rounded-full ${dispositivo.estado.ligado ? 'bg-green-500' : 'bg-gray-300'}`}></div>
          <div className={`dot absolute left-1 top-1 w-6 h-6 rounded-full bg-white transition ${dispositivo.estado.ligado ? 'translate-x-6' : ''}`}></div>
        </div>
        <span className="ml-3">{dispositivo.estado.ligado ? 'Ligado' : 'Desligado'}</span>
      </label>

      {/* Temperatura Interna */}
      <div>
        <label>Temperatura Interna: {dispositivo.estado.temperatura_interna}°C</label>
        <input
          type="range"
          min="-5"
          max="10"
          value={dispositivo.estado.temperatura_interna}
          onChange={(e) => onUpdate('temperatura_interna', Number(e.target.value))}
          className="w-full accent-green-500"
        />
      </div>

      <p>Consumo: {dispositivo.estado.consumoWatts}W</p>
    </div>
  );
}
