import React from 'react';

export default function CardChuveiroEletrico({ dispositivo, onToggle, onUpdate }) {
  return (
    <div className="border p-4 rounded bg-red-50 shadow space-y-3">
      <h2 className="text-lg font-semibold">{dispositivo.nome}</h2>

      {/* Switch */}
      <label className="flex items-center cursor-pointer">
        <div className="relative">
          <input
            type="checkbox"
            className="sr-only"
            checked={dispositivo.estado.ligado}
            onChange={onToggle}
          />
          <div className={`block w-14 h-8 rounded-full ${dispositivo.estado.ligado ? 'bg-green-500' : 'bg-gray-300'}`}></div>
          <div className={`dot absolute left-1 top-1 w-6 h-6 rounded-full bg-white transition ${dispositivo.estado.ligado ? 'translate-x-6' : ''}`}></div>
        </div>
        <span className="ml-3">{dispositivo.estado.ligado ? 'Ligado' : 'Desligado'}</span>
      </label>

      {/* Níveis de temperatura */}
      <div>
        <label>Temperatura:</label>
        <select
          value={dispositivo.estado.temperatura}
          onChange={(e) => onUpdate('temperatura', e.target.value)}
          className="ml-2 border rounded p-1"
        >
          {dispositivo.config.niveis_temperatura.map((nivel) => (
            <option key={nivel} value={nivel}>{nivel}</option>
          ))}
        </select>
      </div>

      <p>Consumo: {dispositivo.estado.consumoWatts}W</p>
    </div>
  );
}
