// ✅ src/context/casaReducer.js
export const initialState = {
  residencia: null,
};

export function casaReducer(state, action) {
  switch (action.type) {
    case 'SET_RESIDENCIA':
      return { ...state, residencia: action.payload };

    case 'TOGGLE_DISPOSITIVO': {
      const { comodoId, dispositivoId } = action.payload;
      const novaResidencia = { ...state.residencia };

      const dispositivos = novaResidencia[comodoId].dispositivos.map((d) => {
        if (d.id === dispositivoId) {
          const novoEstado = {
            ...d.estado,
            ligado: !d.estado.ligado,
          };
          return { ...d, estado: novoEstado };
        }
        return d;
      });

      novaResidencia[comodoId].dispositivos = dispositivos;
      return { ...state, residencia: novaResidencia };
    }

    // 🆕 Novo case para atualizar campos do estado
    case 'UPDATE_DISPOSITIVO': {
      const { comodoId, dispositivoId, campo, valor } = action.payload;
      const novaResidencia = { ...state.residencia };

      novaResidencia[comodoId].dispositivos = novaResidencia[comodoId].dispositivos.map((d) => {
        if (d.id === dispositivoId) {
          return { ...d, estado: { ...d.estado, [campo]: valor } };
        }
        return d;
      });

      return { ...state, residencia: novaResidencia };
    }

    default:
      return state;
  }
}
