import React, { useContext, useMemo } from "react";
import { CasaContext } from "../context/CasaContext";
import { BarChart, Bar, XAxis, YAxis, Tooltip, CartesianGrid, ResponsiveContainer, Cell } from "recharts";

export default function Dashboard() {
  const { state } = useContext(CasaContext);
  const comodos = state?.residencia ? Object.values(state.residencia) : [];

  const { totalLuzesLigadas, consumoTotal, temperaturaMedia, consumoPorComodo } = useMemo(() => {
    let luzesLigadas = 0;
    let consumo = 0;
    let somaTemperaturas = 0;
    let contadorTemperaturas = 0;
    let consumoComodo = [];

    comodos.forEach(comodo => {
      const dispositivos = Array.isArray(comodo?.dispositivos) ? comodo.dispositivos : [];
      let consumoComodoAtual = 0;

      dispositivos.forEach(dispositivo => {
        const ligado = dispositivo.estado?.ligado;
        const watts = dispositivo.estado?.consumoWatts || 0;

        if (dispositivo.tipo === "luz" && ligado) {
          luzesLigadas++;
        }
        if (ligado && watts > 0) {
          consumo += watts;
          consumoComodoAtual += watts;
        }
        if (typeof dispositivo.estado?.temperatura === "number") {
          somaTemperaturas += dispositivo.estado.temperatura;
          contadorTemperaturas++;
        }
      });

      consumoComodo.push({
        name: comodo.nome,
        consumo: consumoComodoAtual
      });
    });

    return {
      totalLuzesLigadas: luzesLigadas,
      consumoTotal: consumo,
      temperaturaMedia: contadorTemperaturas > 0 ? (somaTemperaturas / contadorTemperaturas).toFixed(1) : 0,
      consumoPorComodo: consumoComodo
    };
  }, [comodos]);

  // Função para definir cor do consumo
  const getBarColor = (value) => {
    if (value <= 200) return "#28A745"; // Verde
    if (value <= 500) return "#FFC107"; // Amarelo
    return "#DC3545"; // Vermelho
  };

  return (
    <div style={{ padding: "20px", fontFamily: "Arial, sans-serif", backgroundColor: "#f9f9f9", minHeight: "100vh" }}>
      <h1 style={{ marginBottom: "20px", color: "#333" }}>🏠 Dashboard da Casa</h1>

      {/* Estatísticas agregadas */}
      <div style={{
        display: "flex",
        gap: "20px",
        marginBottom: "30px",
        flexWrap: "wrap"
      }}>
        <StatCard title="Luzes Ligadas" value={totalLuzesLigadas} color="#FFC107" />
        <StatCard title="Consumo Total" value={`${consumoTotal} W`} color="#17A2B8" />
        <StatCard title="Temperatura Média" value={`${temperaturaMedia} °C`} color="#28A745" />
      </div>

      {/* Gráfico de Consumo */}
      {consumoPorComodo.length > 0 && (
        <div style={{
          background: "#fff",
          padding: "20px",
          borderRadius: "10px",
          boxShadow: "0 2px 6px rgba(0,0,0,0.1)",
          marginBottom: "30px"
        }}>
          <h2 style={{ marginBottom: "20px", color: "#333" }}>📊 Consumo por Cômodo</h2>
          <ResponsiveContainer width="100%" height={300}>
            <BarChart data={consumoPorComodo}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="name" />
              <YAxis />
              <Tooltip />
              <Bar dataKey="consumo">
                {consumoPorComodo.map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={getBarColor(entry.consumo)} />
                ))}
              </Bar>
            </BarChart>
          </ResponsiveContainer>
        </div>
      )}

      {/* Lista de cômodos */}
      <div style={{ display: "grid", gap: "15px", gridTemplateColumns: "repeat(auto-fit, minmax(250px, 1fr))" }}>
        {comodos.map((comodo, index) => (
          <div
            key={index}
            style={{
              background: "#fff",
              padding: "15px",
              borderRadius: "10px",
              boxShadow: "0 2px 6px rgba(0,0,0,0.1)",
              transition: "transform 0.2s",
              cursor: "pointer"
            }}
            onMouseEnter={e => e.currentTarget.style.transform = "scale(1.02)"}
            onMouseLeave={e => e.currentTarget.style.transform = "scale(1)"}
          >
            <h3 style={{ margin: "0 0 10px", color: "#333" }}>{comodo.nome}</h3>
            <p style={{ margin: 0, color: "#666" }}>{comodo.dispositivos?.length || 0} dispositivos</p>
          </div>
        ))}
      </div>
    </div>
  );
}

// Componente de cartão de estatísticas
function StatCard({ title, value, color }) {
  return (
    <div style={{
      flex: "1",
      minWidth: "200px",
      background: "#fff",
      padding: "20px",
      borderRadius: "10px",
      boxShadow: "0 2px 6px rgba(0,0,0,0.1)",
      display: "flex",
      flexDirection: "column",
      alignItems: "center",
      transition: "transform 0.2s"
    }}
      onMouseEnter={e => e.currentTarget.style.transform = "scale(1.05)"}
      onMouseLeave={e => e.currentTarget.style.transform = "scale(1)"}
    >
      <h3 style={{ marginBottom: "10px", color: "#333" }}>{title}</h3>
      <p style={{ fontSize: "1.5em", fontWeight: "bold", color }}>{value}</p>
    </div>
  );
}
