import React from 'react';

export default function CardLuz({ dispositivo, onToggle, onUpdate }) {
  return (
    <div className="border p-4 rounded bg-yellow-50 shadow space-y-3">
      <h2 className="text-lg font-semibold">{dispositivo.nome}</h2>

      {/* Switch Liga/Desliga */}
      <label className="flex items-center cursor-pointer">
        <div className="relative">
          <input
            type="checkbox"
            className="sr-only"
            checked={dispositivo.estado.ligado}
            onChange={onToggle}
          />
          <div className={`block w-14 h-8 rounded-full ${dispositivo.estado.ligado ? 'bg-green-500' : 'bg-gray-300'}`}></div>
          <div className={`dot absolute left-1 top-1 w-6 h-6 rounded-full bg-white transition ${dispositivo.estado.ligado ? 'translate-x-6' : ''}`}></div>
        </div>
        <span className="ml-3">{dispositivo.estado.ligado ? 'Ligado' : 'Desligado'}</span>
      </label>

      {/* Intensidade */}
      {dispositivo.config?.dimmerizavel && (
        <div>
          <label>Intensidade: {dispositivo.estado.intensidade}%</label>
          <input
            type="range"
            min="0"
            max="100"
            value={dispositivo.estado.intensidade}
            onChange={(e) => onUpdate('intensidade', Number(e.target.value))}
            className="w-full accent-yellow-500"
          />
        </div>
      )}

      {/* Cor */}
      {dispositivo.config?.suportaCor && (
        <div>
          <label>Cor:</label>
          <input
            type="color"
            value={dispositivo.estado.cor}
            onChange={(e) => onUpdate('cor', e.target.value)}
            className="ml-2"
          />
        </div>
      )}

      <p>Consumo: {dispositivo.estado.consumoWatts}W</p>
    </div>
  );
}
