import { createContext, useReducer, useEffect } from 'react';
import { casaReducer, initialState } from './casaReducer';

export const CasaContext = createContext();

export const CasaProvider = ({ children }) => {
  const [state, dispatch] = useReducer(casaReducer, initialState);

  useEffect(() => {
    fetch('/dadosResidencia.json')
      .then(res => res.json())
      .then(data => {
        dispatch({ type: 'SET_RESIDENCIA', payload: data.residencia });
      });
  }, []);

  return (
    <CasaContext.Provider value={{ state, dispatch }}>
      {children}
    </CasaContext.Provider>
  );
};
