import { useContext } from 'react';
import { useParams } from 'react-router-dom';
import { CasaContext } from '../context/CasaContext';
import CardLuz from '../components/cards/CardLuz';
import CardArCondicionado from '../components/cards/CardArCondicionado';
import CardSmartTV from '../components/cards/CardSmartTV';
import CardEletrodomestico from '../components/cards/CardEletrodomestico';
import CardChuveiroEletrico from '../components/cards/CardChuveiroEletrico';
import CardGenerico from '../components/cards/CardGenerico';

export default function ComodoDetalhado() {
  const { id } = useParams();
  const { state, dispatch } = useContext(CasaContext);

  const comodo = state.residencia?.[id];
  if (!comodo) return <p>Carregando...</p>;

  const renderCard = (dispositivo) => {
    const onToggle = () =>
      dispatch({
        type: 'TOGGLE_DISPOSITIVO',
        payload: { comodoId: id, dispositivoId: dispositivo.id },
      });

    const onUpdate = (campo, valor) =>
      dispatch({
        type: 'UPDATE_DISPOSITIVO',
        payload: { comodoId: id, dispositivoId: dispositivo.id, campo, valor },
      });

    switch (dispositivo.tipo) {
      case 'luz':
        return <CardLuz dispositivo={dispositivo} onToggle={onToggle} onUpdate={onUpdate} />;
      case 'ar_condicionado':
        return <CardArCondicionado dispositivo={dispositivo} onToggle={onToggle} onUpdate={onUpdate} />;
      case 'smart_tv':
        return <CardSmartTV dispositivo={dispositivo} onToggle={onToggle} onUpdate={onUpdate} />;
      case 'eletrodomestico':
        return <CardEletrodomestico dispositivo={dispositivo} onToggle={onToggle} onUpdate={onUpdate} />;
      case 'chuveiro_eletrico':
        return <CardChuveiroEletrico dispositivo={dispositivo} onToggle={onToggle} onUpdate={onUpdate} />;
      default:
        return <CardGenerico dispositivo={dispositivo} onToggle={onToggle} onUpdate={onUpdate} />;
    }
  };

  return (
    <div>
      <h1 className="text-2xl font-bold mb-4">{comodo.nome}</h1>
      <div className="grid gap-4">
        {comodo.dispositivos.map((d) => (
          <div key={d.id}>{renderCard(d)}</div>
        ))}
      </div>
    </div>
  );
}
