import React from 'react';

export default function CardSmartTV({ dispositivo, onToggle, onUpdate }) {
  return (
    <div className="border p-4 rounded bg-purple-50 shadow space-y-3">
      <h2 className="text-lg font-semibold">{dispositivo.nome}</h2>

      {/* Switch */}
      <label className="flex items-center cursor-pointer">
        <div className="relative">
          <input
            type="checkbox"
            className="sr-only"
            checked={dispositivo.estado.ligado}
            onChange={onToggle}
          />
          <div className={`block w-14 h-8 rounded-full ${dispositivo.estado.ligado ? 'bg-green-500' : 'bg-gray-300'}`}></div>
          <div className={`dot absolute left-1 top-1 w-6 h-6 rounded-full bg-white transition ${dispositivo.estado.ligado ? 'translate-x-6' : ''}`}></div>
        </div>
        <span className="ml-3">{dispositivo.estado.ligado ? 'Ligada' : 'Desligada'}</span>
      </label>

      {/* Volume */}
      <div>
        <label>Volume: {dispositivo.estado.volume}</label>
        <input
          type="range"
          min="0"
          max="100"
          value={dispositivo.estado.volume}
          onChange={(e) => onUpdate('volume', Number(e.target.value))}
          className="w-full accent-purple-500"
        />
      </div>

      {/* Canal/App */}
      <div>
        <label>Canal/App:</label>
        <input
          type="text"
          value={dispositivo.estado.canal_app}
          onChange={(e) => onUpdate('canal_app', e.target.value)}
          className="ml-2 border rounded p-1"
        />
      </div>

      <p>Consumo: {dispositivo.estado.consumoWatts}W</p>
    </div>
  );
}
