import { useContext } from 'react';
import { CasaContext } from '../context/CasaContext';
import { Link } from 'react-router-dom';

export default function Navbar() {
  const { state } = useContext(CasaContext);

  if (!state.residencia) return null;

  return (
    <nav className="mb-6 border-b pb-2 flex gap-4 flex-wrap">
      <Link to="/" className="font-bold">🏠 Início</Link>
      {Object.entries(state.residencia).map(([id, comodo]) => (
        <Link key={id} to={`/comodo/${id}`} className="text-blue-600 hover:underline">
          {comodo.nome}
        </Link>
      ))}
    </nav>
  );
}
