import React from 'react';

export default function CardGenerico({ dispositivo, onToggle }) {
  return (
    <div className="border p-4 rounded bg-gray-50 shadow">
      <h2 className="text-lg font-semibold">{dispositivo.nome}</h2>
      <p>Tipo: {dispositivo.tipo}</p>
      {Object.entries(dispositivo.estado).map(([key, value]) => (
        <p key={key}>
          {key}: {String(value)}
        </p>
      ))}
      <button
        className="mt-2 px-4 py-1 bg-gray-500 text-white rounded"
        onClick={onToggle}
      >
        {dispositivo.estado.ligado ? 'Desligar' : 'Ligar'}
      </button>
    </div>
  );
}
